<template>
	<view>
		<u-tabbar activeColor="#ea9518" :value="value1" @change="change1" :safeAreaInsetBottom="true">
			<u-tabbar-item text="首页">
				<image class="u-page__item__slot-icon" slot="active-icon" src="../static/tarbar/home_on.png" mode="widthFix"></image>
				<image class="u-page__item__slot-icon" slot="inactive-icon" src="../static/tarbar/home.png" mode="widthFix"></image>
			</u-tabbar-item>
			<u-tabbar-item text="订单">
				<image class="u-page__item__slot-icon" slot="active-icon" src="../static/tarbar/order_on.png" mode="widthFix"></image>
				<image class="u-page__item__slot-icon" slot="inactive-icon" src="../static/tarbar/order.png" mode="widthFix"></image>
			</u-tabbar-item>
			<u-tabbar-item text="我的">
				<image class="u-page__item__slot-icon" slot="active-icon" src="../static/tarbar/my_on.png" mode="widthFix"></image>
				<image class="u-page__item__slot-icon" slot="inactive-icon" src="../static/tarbar/my.png" mode="widthFix"></image>
			</u-tabbar-item>
		</u-tabbar>
	</view>
</template>
<script>
	export default {
		props: {
		    current: { type: [Number, String], default: 0 },
		},
		data() {
			return {
				value1:this.current,
			}
		},
		methods: {
			change1(name) {
				switch (name) {
					case 0:
						uni.switchTab({
							url: '/pages/index'
						})
						break;
					case 1:
						uni.switchTab({
							url: '/pages/dingdan'
						})
						break;
					case 2:
						uni.switchTab({
							url: '/pages/wode'
						})
						break;
				}
			},
		}
	}
</script>
<style scoped>
	image{
		width: 20px;
		height: 20px;
	}
</style>
